'use client';

import React, { useMemo } from 'react';
import { useParams } from 'next/navigation';
import { 
  ShieldCheck, Database, Info, Cpu, Clock, 
  Globe, Landmark, FileText, Sparkles 
} from 'lucide-react';
import Link from 'next/link';
import { treatyData } from '@/data/treaties';

export default function TaxTreatyDetailPage() {
  const params = useParams();
  const slug = params.slug as string;

  // 1. Logika Pencocokan Slug yang Kuat
  const data = useMemo(() => {
    return treatyData.find(t => {
      const [source, target] = t.pair.split('-');
      // Mengubah "United States-United Kingdom" menjadi "tax-treaty-united-states-to-united-kingdom"
      const generatedSlug = `tax-treaty-${source.toLowerCase().replace(/\s+/g, '-')}-to-${target.toLowerCase().replace(/\s+/g, '-')}`;
      return generatedSlug === slug;
    });
  }, [slug]);

  // Jika data tidak ditemukan
  if (!data) return (
    <div className="min-h-screen flex items-center justify-center bg-[#FDFCFB] font-serif italic text-gray-400">
      Jurisdictional data for "{slug}" not found in public registry.
    </div>
  );

  const [sourceName, targetName] = data.pair.split('-');

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      
      {/* HERO SECTION */}
      <section className="max-w-6xl mx-auto pt-24 pb-12 px-8 text-center space-y-6">
        <div className="inline-flex items-center gap-3 px-6 py-2 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.4em]">
          <Database className="w-4 h-4" /> Official Registry Data
        </div>
        <h1 className="text-6xl font-serif italic leading-tight text-black">
          {sourceName} <span className="text-[#D4AF37]">to</span> {targetName}
        </h1>
        <p className="max-w-2xl mx-auto text-gray-400 font-light italic text-[17px] leading-relaxed">
          Standard Withholding Tax (WHT) rates under the 2026 bilateral agreement framework.
        </p>
      </section>

      {/* DATA DISPLAY GRID */}
      <section className="max-w-5xl mx-auto px-8 mb-20">
        <div className="bg-white border border-gray-100 rounded-[4rem] overflow-hidden shadow-2xl shadow-gray-100/50">
          <div className="grid md:grid-cols-3 divide-y md:divide-y-0 md:divide-x divide-gray-50">
            <div className="p-12 space-y-4 hover:bg-[#FDFCFB] transition-colors text-center">
              <p className="text-[11px] font-black uppercase tracking-widest text-gray-400">Dividends</p>
              <p className="text-6xl font-serif italic text-black">{data.dividend}</p>
            </div>
            <div className="p-12 space-y-4 hover:bg-[#FDFCFB] transition-colors text-center">
              <p className="text-[11px] font-black uppercase tracking-widest text-gray-400">Interest</p>
              <p className="text-6xl font-serif italic text-black">{data.interest}</p>
            </div>
            <div className="p-12 space-y-4 hover:bg-[#FDFCFB] transition-colors text-center">
              <p className="text-[11px] font-black uppercase tracking-widest text-gray-400">Royalties</p>
              <p className="text-6xl font-serif italic text-black">{data.royalty}</p>
            </div>
          </div>
          
          <div className="grid md:grid-cols-2 bg-gray-50/50 border-t border-gray-100 divide-x divide-gray-100">
            <div className="p-8 flex items-center gap-6">
              <Clock className="text-[#D4AF37]" size={20} />
              <div className="text-left">
                <p className="text-[10px] font-black uppercase tracking-widest text-gray-400">Residency Rule</p>
                <p className="text-[16px] font-serif italic text-black">{data.residency}</p>
              </div>
            </div>
            <div className="p-8 flex items-center gap-6">
              <ShieldCheck className="text-[#D4AF37]" size={20} />
              <div className="text-left">
                <p className="text-[10px] font-black uppercase tracking-widest text-gray-400">Primary Article</p>
                <p className="text-[16px] font-serif italic text-black">{data.article}</p>
              </div>
            </div>
          </div>

          {/* CTA TO AI VAULT */}
          <div className="p-10 bg-black flex flex-col md:flex-row justify-between items-center gap-8 border-t border-gray-800 text-center md:text-left">
            <div className="flex items-start gap-4 text-[14px] text-gray-400 italic font-light max-w-xl leading-relaxed">
              <Info size={20} className="text-[#D4AF37] shrink-0 mt-1" /> 
              <p>Reference Note: <span className="text-white">{data.notes}</span></p>
            </div>
            <Link href="/ai-vault" className="flex items-center gap-3 text-[12px] font-black uppercase tracking-widest bg-[#D4AF37] text-black px-10 py-5 rounded-2xl hover:bg-white transition-all shadow-lg">
              <Cpu size={18} /> Open AI Tax Vault
            </Link>
          </div>
        </div>
      </section>

      {/* SEO CONTENT SECTION */}
      <section className="max-w-4xl mx-auto px-8 space-y-12 text-left border-t border-gray-100 pt-20">
        <h2 className="text-3xl font-serif italic text-black underline decoration-[#D4AF37] decoration-2 underline-offset-8">Technical Review: {sourceName} â€” {targetName}</h2>
        <div className="space-y-6 text-gray-500 font-light leading-relaxed text-[17px]">
          <p>
            The double taxation avoidance agreement (DTAA) between {sourceName} and {targetName} establishes a comprehensive framework for cross-border income flows. Under the current 2026 guidelines, withholding taxes for dividends are set at {data.dividend}, while interest payments follow the {data.interest} threshold as defined by {data.article}.
          </p>
          <p>
            Qualification for these preferential rates requires strict adherence to the "{data.residency}" residency test. Corporate and individual taxpayers must maintain valid documentation, including the Certificate of Residence (CoR), to prevent the application of standard domestic statutory rates. 
          </p>
          <p>
            GlobalTax Laboratory provides this data for professional modeling. For complex scenarios involving multilateral instruments (MLI) or specific anti-abuse provisions (GAAR), we recommend using the <Link href="/ai-vault" className="text-black font-bold hover:text-[#D4AF37] underline">AI Tax Vault</Link> for an article-by-article legal simulation based on your specific transaction volume.
          </p>
          {/* Tambahkan lebih banyak teks di sini untuk mencapai 1000 kata */}
        </div>
      </section>
    </div>
  );
}